.header__nav-link {
    position: relative;
    margin: 0 1.6rem 0 .9rem;

    &:link,
    &:visited {
        text-decoration: none;
        font-family: Montserrat;
        font-weight: 700;
        font-size: 1.4rem;
        color: $color-white;
        text-transform: uppercase;
    }
    
    &:hover,
    &:active,
    &.active {
        &::before {
            opacity: 1;
            visibility: visible;
        }
    }

    &::before {
        content: '';
        width: 100%;
        height: 2px;
        position: absolute;
        bottom: -6px;
        opacity: 0;
        visibility: hidden;
        background-color: $color-white;
        transition: all .3s ease;
    }

    &::after {
        content: '+';
        width: 7px;
        height: 7px;
        font-size: 14px;
        font-weight: 600;
        position: absolute;
        right: -15px;
    }

    &:last-child {
        &::after {
            content: '';
        }

        margin-right: 0;
    }

    &:first-child {
        margin-left: 0;
    }
}